<template>
  <div class="">
    <el-input v-model="input" placeholder="请输入内容" prefix-icon="el-icon-search" class="search-input"></el-input>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      input:''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .search-input{
    height: 100px;
    width:50%;
  }
  el-input {
    height: 2rem ;
    font-size:2rem;
  }
  .el-input__inner{
    height: 200px !important;
  }
</style>



